

import { Directionality } from '@angular/cdk/bidi';
import {
  ChangeDetectionStrategy,
  ChangeDetectorRef,
  Component,
  ComponentFactory,
  ComponentFactoryResolver,
  ComponentRef,
  Directive,
  ElementRef,
  EventEmitter,
  Host,
  Input,
  Optional,
  Output,
  Renderer2,
  TemplateRef,
  ViewContainerRef,
  ViewEncapsulation
} from '@angular/core';
import { zoomBigMotion } from 'tds-ui/core/animation';
import { NgClassInterface, NgStyleInterface, TDSTSType } from 'tds-ui/core/config';
import { InputBoolean } from 'tds-ui/shared/utility';
import { isTooltipEmpty, TDSPropertyMapping, TDSTooltipBaseDirective, TDSToolTipComponent, TDSTooltipTrigger } from 'tds-ui/tooltip';

@Directive({
  selector: '[tds-popover]',
  exportAs: 'tdsPopover',
  host: {
    '[class.tds-popover-open]': 'visible'
  }
})
export class TDSPopoverDirective extends TDSTooltipBaseDirective {
  // readonly _tdsModuleName: TDSConfigKey = TDS_CONFIG_MODULE_NAME;

  @Input('popoverTitle') override title?: TDSTSType;
  @Input('popoverContent') override content?: TDSTSType;
  @Input('popoverFooter') override footer?: TDSTSType;
  @Input('tds-popover') override directiveTitle?: TDSTSType | null;
  @Input('popoverTrigger') override trigger?: TDSTooltipTrigger = 'hover';
  @Input('popoverPlacement') override placement?: string | string[] = 'top';
  @Input('popoverOrigin') override origin?: ElementRef<HTMLElement>;
  @Input('popoverVisible') override visible?: boolean;
  @Input('popoverMouseEnterDelay') override mouseEnterDelay?: number;
  @Input('popoverMouseLeaveDelay') override mouseLeaveDelay?: number;
  @Input('popoverOverlayClassName') override overlayClassName?: string;
  @Input('popoverOverlayStyle') override overlayStyle?: NgStyleInterface;
  @Input() @InputBoolean()  popoverBackdrop?: boolean = false;
  @Input('popoverAutoClose') autoClose?: boolean = true;
  // tslint:disable-next-line:no-output-rename
  @Output('popoverVisibleChange') readonly visibleChange = new EventEmitter<boolean>();

  componentRef: ComponentRef<TDSPopoverComponent> = this.hostView.createComponent(TDSPopoverComponent);
  protected getProxyPropertyMap(): TDSPropertyMapping {

    return {
      popoverBackdrop: ['backdrop', () => this.popoverBackdrop],
      autoClose: ['autoClose', () => this.autoClose],
      ...super.getProxyPropertyMap()
    };
  }

  constructor(
    elementRef: ElementRef,
    hostView: ViewContainerRef,
    resolver: ComponentFactoryResolver,
    renderer: Renderer2,
    // @Host() @Optional() public noAnimation?: TDSNoAnimationDirective,
    // tdsConfigService?: TDSConfigService
  ) {
    super(elementRef, hostView, resolver, renderer);
  }

}

@Component({
  selector: 'tds-popover',
  exportAs: 'tdsPopoverComponent',
  animations: [zoomBigMotion],
  changeDetection: ChangeDetectionStrategy.OnPush,
  encapsulation: ViewEncapsulation.None,
  preserveWhitespaces: false,
  template: `
    <ng-template
      #overlay="cdkConnectedOverlay"
      cdkConnectedOverlay
      [cdkConnectedOverlayHasBackdrop]="hasBackdrop"
      [cdkConnectedOverlayOrigin]="origin"
      [cdkConnectedOverlayPositions]="_positions"
      [cdkConnectedOverlayOpen]="_visible"
      [cdkConnectedOverlayPush]="true"
      (overlayOutsideClick)="onClickOutside($event)"
      (detach)="hide()"
      (positionChange)="onPositionChange($event)"
    >
      <div
        class="tds-popover relative "    
        [ngClass]="_classMap"
        [ngStyle]="overlayStyle"
        [@zoomBigMotion]="'active'"
      >
        <div class="tds-popover-content relative"  [ngClass]="
        {
          'pt-2.5': _isBottom(),
          'pr-2.5': _isLeft(),
          'pl-2.5': _isRight(),
          'pb-2.5': _isTop()
        }">
          <div class="tds-popover-arrow absolute block bg-transparent pointer-events-none"
          [ngStyle]="_contentStyleArrow" >
            <span class="tds-popover-arrow-content block border-8 border-white  dark:border-d-neutral-3-300" [ngStyle]="_contentStyleArrowContent"></span>
          </div>
          <div class="tds-popover-inner  shadow-1-xl bg-white dark:bg-d-neutral-3-300 rounded-md" role="tooltip">
            <div>
              <div class="tds-popover-title font-semibold text-neutral-1-900 dark:text-d-neutral-1-900 text-title-1 p-4 border-b border-neutral-2-200 dark:border-d-neutral-2-200" *ngIf="title">
                <ng-container *tdsStringTemplateOutlet="title">{{ title }}</ng-container>
              </div>
              <div class="p-4">
                <div class="tds-popover-inner-content text-neutral-1-900 dark:text-d-neutral-1-900 ">
                  <ng-container *tdsStringTemplateOutlet="content">{{ content }}</ng-container>
                </div>
                <div *ngIf="footer" class="tds-popover-inner-footer" [ngClass]="{'pt-8':isFooterString}">
                  <ng-container *tdsStringTemplateOutlet="footer">{{ footer }}</ng-container>
                </div>
              </div>            
            </div>
          </div>
        </div>
      </div>
    </ng-template>
  `
})
export class TDSPopoverComponent extends TDSToolTipComponent {
  _prefix = 'tds-popover';
  _classPopopverContent: NgClassInterface = {};
  constructor(
    cdr: ChangeDetectorRef,
    @Optional() directionality: Directionality,
    // @Host() @Optional() public noAnimation?: TDSNoAnimationDirective
  ) {
    super(cdr, directionality);
  }

  get hasBackdrop(): boolean {
    return this.trigger === 'click' ? this.backdrop : false;
  }
  get isFooterString() {
    return !isTooltipEmpty(this.footer) && !(this.footer instanceof TemplateRef);
  }
  protected isEmpty(): boolean {
    return isTooltipEmpty(this.title) && isTooltipEmpty(this.content);
  }
  updateStyles(): void {
    const isColorPreset = this.color;
    this._classMap = {
      [this.overlayClassName]: true,
      [`${this._prefix}-placement-${this.preferredPlacement}`]: true,
      [`${this._prefix}-${this.color}`]: isColorPreset,

    };
    this.styleArrow();
  }
  styleArrow() {
    let borderTransparent = '8px solid transparent';
    // let borderColor = '8px solid #fff';
    if (this.preferredPlacement.indexOf("top") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderLeft: borderTransparent,
        borderRight: borderTransparent,
        borderBottom: 0,
      }
    }
    if (this.preferredPlacement.indexOf("bottom") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderLeft: borderTransparent,
        borderRight: borderTransparent,
        borderTop: 0,
      }
    }
    if (this.preferredPlacement.indexOf("left") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderTop: borderTransparent,
        borderRight: 0,
        borderBottom: borderTransparent,

      }
    }
    if (this.preferredPlacement.indexOf("right") > -1) {
      this._contentStyleArrowContent = {
        width: '0',
        height: '0',
        borderTop: borderTransparent,
        borderLeft: 0,
        borderBottom: borderTransparent,

      }
    }
    switch (this.preferredPlacement) {
      //#region top
      case "top":
        this._contentStyleArrow = {
          bottom: '2px',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
      case "topLeft":
        this._contentStyleArrow = {
          bottom: '2px',
          left: '10px',
        }
        break;
      case "topRight":
        this._contentStyleArrow = {
          bottom: '2px',
          right: '10px',
        }
        break;
      //#endregion
      //#region bottom
      case "bottom":
        this._contentStyleArrow = {
          top: '2px',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
      case "bottomLeft":
        this._contentStyleArrow = {
          top: '2px',
          left: '10px',
        }
        break;
      case "bottomRight":
        this._contentStyleArrow = {
          top: '2px',
          right: '10px',
        }
        break;
      //#endregion
      //#region left
      case "left":
        this._contentStyleArrow = {
          right: '2px',
          top: '50%',
          transform: 'translateY(-50%)',
        }
        break;
      case "leftTop":
        this._contentStyleArrow = {
          right: '2px',
          top: '10px'
        }
        break;
      case "leftBottom":
        this._contentStyleArrow = {
          right: '2px',
          bottom: '10px'
        }
        break;
      //#endregion
      //#region right
      case "right":
        this._contentStyleArrow = {
          left: '2px',
          top: '50%',
          transform: 'translateY(-50%)',
        }
        break;
      case "rightTop":
        this._contentStyleArrow = {
          left: '2px',
          top: '10px'
        }
        break;
      case "rightBottom":
        this._contentStyleArrow = {
          left: '2px',
          bottom: '10px'
        }
        break;
      ////#endregion
      default:
        this._contentStyleArrow = {
          bottom: '2px',
          left: '50%',
          transform: 'translateX(-50%)',
        }
        break;
    }

  }
  _isTop() {
    return this.preferredPlacement.indexOf("top") > -1;
  }
  _isLeft() {
    return this.preferredPlacement.indexOf("left") > -1;
  }
  _isRight() {
    return this.preferredPlacement.indexOf("right") > -1;
  }
  _isBottom() {
    return this.preferredPlacement.indexOf("bottom") > -1;
  }

}
